	import {  
	    mapState  
	} from 'vuex';
	import {
	    mapMutations  
	} from 'vuex';
	export default {
		data() {
			return {
				
			};
		},
		computed:{
			...mapState(['hasLogin','unifiedUser'])
		},
		methods:{
			...mapMutations(['logout']), 
  			navTo(url){
				if(!this.hasLogin){
					url = '/pages/public/login';
				}
				if(url){
					uni.navigateTo({
						url
					})
				}   
 			},
			toLoginPage(){
				this.logout();
				//删除token
				uni.removeStorage({
				    key: this.sessionKey  
				}) 
 				setTimeout(()=>{
					uni.navigateBack();
				}, 300)
			},
			//退出登录
			toLogout(){
				let _this=this;
				uni.showModal({
				    content: '确定要退出登录么',
				    success: (e)=>{
				    	if(e.confirm){
							//获取数据
							_this.sendRequest({
								url : "logout",
								data : {},
								method : "get",
 								success:function (res) {
									 _this.toLoginPage(); 
								}
							});    
				    	}
				    }
				});
			},
			//switch
			switchChange(e){
				let statusTip = e.detail.value ? '打开': '关闭';
				this.$api.msg(`${statusTip}消息推送`);
			},

		}
	}
